.footer {
  padding: 0.1% 0;
  background-color: #1d1d20;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  position: absolute;
  bottom: 0;
  width: 100%;
}

.footer p {
  font-weight: 400;
  font-size: 12px;
  color: #fff;
  letter-spacing: 0.05em;
  margin-left: 2%;
}

.footer .copyright {
  font-size: 12px;
  color: #fff;
  text-align: center;
}

a {
  color: #feb9ce;
  text-decoration: none;
}